.mapClass {
  display: flex;
  flex-direction: column;
  left: 100px;
  width: 70vw;
  height: 100vh;
}

canvas {
  position: static !important;
  left: 100px;
}
#deckgl-wrapper {
  left: 100px;
}

.mapContainer {
  height: 70%;
  color: black;
  left: 100px;
}

.button3d2d {
  display: flex;
  flex-direction: row;
  background-color: #ff312e;
  height: 30px;
  margin: 0 auto;
  width: 100%;
  top: 0;
}

.activeButtonStyle {
  display: flex;
  flex-direction: row;
  width: 35vw;
  height: 30px;
  top: 0px;
  background-color: #ff312e;
  text-align: center;
  margin: 0 auto;
}

.inactiveButtonStyle {
  display: flex;
  flex-direction: row;
  width: 35vw;
  height: 30px;
  top: 0px;
  background-color: #2c1364;
  text-align: center;
  margin: 0 auto;
}

.buttonStyle .inactiveButtonStyle span {
  float: center;
  position: relative;
  bottom: 18px;
  text-align: center;
  width: 100%;
}

.buttonStyle .activeButtonStyle span {
  float: center;
  position: relative;
  bottom: 18px;
  text-align: center;
  width: 100%;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.header {
  overflow: hidden;
  background-color: #161022;
  color: white;
  padding: 0px 0px;
  margin: 0 0 0 0;
  height: 90px;
}

.header a {
  float: left;
  color: white;
  text-align: center;
  padding: 0px 15px;
  text-decoration: none;
  font-size: 7px;
  line-height: 25px;
  border-radius: 4px;
}

.header a.logo {
  font-family: "Arial", Arial, Helvetica, sans-serif;
  font-size: 1em;
  width: 120px;
  text-align: left;
  vertical-align: text-top;
  top: 0;
  margin-top: 0px;
  padding: 0 0 0 0;
  color: white;
}

.header a:hover {
  background-color: #ddd;
  color: black;
}

.header h5 {
  margin: -1px;
}

.header a.active {
  background-color: dodgerblue;
  color: white;
}

.header-right {
  float: right;
}

@media screen and (max-width: 500px) {
  .header a {
    float: none;
    display: block;
    text-align: left;
  }
  .header-right {
    float: none;
  }
}
